import React, { Component } from 'react';
import './Event.css';

class Event extends Component {
    state = {
        showingDetails: false
    }

    eventDetails = () => {
        const showingDetails = this.state.showingDetails;
        if (showingDetails === false) {
            this.setState({
                showingDetails: true
            })
        } else {
            this.setState({
                showingDetails: false
            })
        }
    };

    render() {
        const { event } = this.props;
        const eventISODateTime = new Date(event.start.dateTime);
        const dateTime = eventISODateTime.toDateString();
        return <div className="event-container">
            <h1 className="event-summary">{event.summary}</h1>
            <p className="event-date">{dateTime} </p>
            <p className="event-timezone">{event.start.timeZone}</p>
            <p className="event-location">{event.location}</p>
            {this.state.showingDetails && (
                <div className="event-details">
                    <h3 className="about-event">About event:</h3>
                    <a className="details-link" target="_blank" rel="noreferrer" href={event.htmlLink}>See details on Google Calendar</a>
                    <p className="event-description">{event.description}</p>
                </div>
            )}
            <button className="show-hide" onClick={() => this.eventDetails()}> {this.state.showingDetails ? 'hide details' : 'show details'} </button>
        </div >
    }
}

export default Event;